import React from "react";
import {
  Bell,
  ClipboardList,
  CheckCircle2,
  XCircle,
  Info,
  Home,
  Briefcase,
  Wallet,
  Settings,
} from "lucide-react";
import { useNavigate } from "react-router-dom";

// EVzone Driver App  D22 Preferences  Quiz Answer Selected (v1)
// Visual state of the quiz when an answer has been selected but not yet submitted.
// 375x812 phone frame, swipe scrolling in <main>, scrollbar hidden.

const quizQuestion = {
  question:
    "Which of the following is the best way to increase your earnings with EVzone?",
  options: [
    "Accept every trip, even if it feels unsafe",
    "Drive only during quiet hours",
    "Plan around peak hours and use surge zones safely",
    "Cancel trips often if the distance looks long",
  ],
  selectedIndex: 2,
  correctIndex: 2,
};

function BottomNavItem({ icon: Icon, label, active }) {
  return (
    <button
      className={`flex flex-col items-center justify-center flex-1 py-2 text-xs font-medium transition-colors ${
        active ? "text-[#03cd8c]" : "text-slate-500 hover:text-slate-700"
      }`}
    >
      <Icon className="h-5 w-5 mb-0.5" />
      <span>{label}</span>
    </button>
  );
}

function OptionStatic({ index, label, selected }) {
  const base = "w-full rounded-2xl border px-3 py-2.5 text-left text-[11px] font-medium flex items-center justify-between";

  if (selected) {
    return (
      <div
        className={`${base} border-[#03cd8c] bg-[#e6fff7] text-slate-900 shadow-sm`}
      >
        <span className="pr-2">{label}</span>
        <CheckCircle2 className="h-4 w-4 text-[#03cd8c]" />
      </div>
    );
  }

  return (
    <div
      className={`${base} border-slate-200 bg-white text-slate-800`}
    >
      <span className="pr-2">{label}</span>
    </div>
  );
}

export default function QuizAnswerSelectedScreen() {
  const [nav] = React.useState("settings");
  const navigate = useNavigate();

  return (
    <div className="min-h-screen flex justify-center bg-[#0f172a] py-4">
      {/* Local style: hide scrollbars but keep swipe scrolling */}
      <style>{`
        .scrollbar-hide::-webkit-scrollbar { width: 0; height: 0; }
        .scrollbar-hide { -ms-overflow-style: none; scrollbar-width: none; }
      `}</style>

      <div className="w-[375px] h-[812px] bg-white rounded-[32px] shadow-2xl overflow-hidden flex flex-col">
        {/* Header */}
        <header className="flex items-center justify-between px-4 pt-4 pb-2">
          <div className="flex items-center space-x-2">
            <div className="flex h-8 w-8 items-center justify-center rounded-full bg-[#e6fff7]">
              <ClipboardList className="h-4 w-4 text-[#03cd8c]" />
            </div>
            <div className="flex flex-col items-start">
              <span className="text-[11px] uppercase tracking-[0.18em] text-slate-400">
                Preferences  Training
              </span>
              <h1 className="text-base font-semibold text-slate-900">
                Driver info session quiz
              </h1>
            </div>
          </div>
          <button className="relative inline-flex h-9 w-9 items-center justify-center rounded-full bg-slate-100 text-slate-700">
            <Bell className="h-4 w-4" />
            <span className="absolute -top-0.5 -right-0.5 inline-flex h-3 w-3 items-center justify-center rounded-full bg-[#f77f00]" />
          </button>
        </header>

        {/* Content */}
        <main className="flex-1 px-4 pb-4 space-y-4 overflow-y-auto scrollbar-hide">
          {/* Question */}
          <section className="space-y-2 pt-1">
            <p className="text-[11px] font-medium text-slate-500">
              Question 1 of 3
            </p>
            <p className="text-sm font-semibold text-slate-900">
              {quizQuestion.question}
            </p>
          </section>

          {/* Options  with one selected */}
          <section className="space-y-2">
            {quizQuestion.options.map((opt, idx) => (
              <OptionStatic
                key={idx}
                index={idx}
                label={opt}
                selected={quizQuestion.selectedIndex === idx}
              />
            ))}
          </section>

          {/* Hint / footer */}
          <section className="space-y-2 pt-1 pb-4">
            <div className="rounded-2xl border border-slate-100 bg-slate-50 px-3 py-3 text-[11px] text-slate-600 flex items-start space-x-2">
              <Info className="h-4 w-4 mt-0.5 text-slate-500" />
              <p>
                When you&apos;re happy with your choice, tap <span className="font-semibold">Submit answer</span>.
                You can change your selection before submitting.
              </p>
            </div>

            <button
              type="button"
              onClick={() => navigate("/driver/training/quiz/passed")}
              className="w-full rounded-full py-2.5 text-sm font-semibold shadow-sm bg-[#03cd8c] text-slate-900 hover:bg-[#02b77c]"
            >
              Submit answer
            </button>
          </section>
        </main>

        {/* Bottom navigation  Settings active (Preferences context) */}
        <nav className="border-t border-slate-100 bg-white/95 backdrop-blur flex">
          <BottomNavItem icon={Home} label="Home" active={nav === "home"} />
          <BottomNavItem icon={Briefcase} label="Manager" active={nav === "manager"} />
          <BottomNavItem icon={Wallet} label="Wallet" active={nav === "wallet"} />
          <BottomNavItem icon={Settings} label="Settings" active={nav === "settings"} />
        </nav>
      </div>
    </div>
  );
}
